import React from 'react';
import TransactionTable from'./TransactionTable';

function Transactions()
{
    return (<div class="transactions">
    <div className="theader">
    <p class="theading">Recent Transactions</p>
    <div className="exportbutton">
    <i className="fas fa-upload exporticon"></i>
    <p className="exporttext">Export</p>
    </div>
    </div>
    <div class="transactionlist">
        <div class="transactiontype">
        <div class="incoming"><div class="incomingtext">Incoming</div><div class="selectedtype"></div></div>
        <div class="invoices">Invoices</div>
        </div>
    </div>
    <hr></hr>
    <TransactionTable/>
    </div>);
}

export default Transactions;